<template>
    <div class="container">
        <input type="number" v-model="ping1" @change="$emit('compare1',ping1)">
        <div class="middle-co">
            <div><span>VS</span></div>
            <button @click="runIt">Play</button>
        </div>
        <input type="number" v-model="ping2" @change="$emit('compare2',ping2)">
    </div>
</template>

<script>

export default {
    name: 'pingOption',
    data() {
        return {
            ping1: "",
            ping2: "",
            firstBarClass: "process-bar1",
            secondBarClass: "process-bar2",
            index: ""
        }
    },
    methods: {
        runIt() {
            let bars = document.getElementsByClassName("cbar");
            bars[0].style.animationDuration = this.ping1 + "ms";
            bars[1].style.animationDuration = this.ping2 + "ms";
            for (var i = 0; i < bars.length; i++) {
                bars[i].classList.remove(this.firstBarClass);
                bars[i].className += ` ${this.secondBarClass}`;
            }
            this.index = this.firstBarClass;
            this.firstBarClass = this.secondBarClass;
            this.secondBarClass = this.index;
        }
    }
}
</script>

<style scoped>
    .container {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .middle-co {
        text-align: center;
    }
    input {
        margin: 5px 25px;
        width: 80px;
        height: 40px;
        font-size: 18px;
        border-radius: 5px;
        border-color: #616161;
        text-align: center;
    }input:focus {
        outline: none;
    }
    span {
        font-size: 20px;
    }
    button {
        border: none;
        width: 50px;
        height: 40px;
        border-radius: 10px;
        cursor: pointer;
        transition: all 150ms;
    }button:focus {
        outline: none;
        background: #d4d4d4;
    }button:hover {
        font-size: 17px;
        background: #ebebeb;
    }
</style>

