<template>
    <div>
        <pingOption v-on:compare1="store1" v-on:compare2="store2"/>
        <compBar :animationSpeed="ping1"/>
        <compBar :animationSpeed="ping2"/>
    </div>
</template>

<script>
import compBar from "./compBar.vue";
import pingOption from "./pingOption.vue";

export default {
    name: 'compareSec',
    data() {
        return {
            ping1: "",
            ping2: ""
        }
    },
    components: {
        compBar,
        pingOption
    },
    
    methods: {
        store1(pingSpeed) {
            this.ping1 = pingSpeed;
        },
        store2(pingSpeed) {
            this.ping2 = pingSpeed;
        }
    }
}
</script>

